.dot {
  cursor: pointer;
  background-color: #e0e0e0;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  transition: all .3s;
}

.active, .dot:hover {
  background-color: #717171;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: .4;
  }

  to {
    opacity: 1;
  }
}

